var fs = require('fs')
var html = require('choo/html')
var raw = require('choo/html/raw')
var marked = require('marked')

var content = raw(marked(
  fs.readFileSync(__dirname + '/../content/cv.md', 'utf8')
))

module.exports = function cv () {
  return html`
    <main class="code ma4 pa4 lh-copy bg-white mw8">
      <p><a href="/"> Back</a></p>
      ${content}
    </main>
  `
}
