var html = require('choo/html')
var css = require('sheetify')

var prefix = css`
  @media (max-width: 425px) {
    /* smaller font on narrow screens
     * so "renee.kooi.me" doesnt cause horizontal scroll */
    :host > .f1 {
      font-size: 2rem;
    }
  }
`

var WIZARD = ''
var TRAIN = ''

function Link (href, title) {
  return html`
    <a class="dark-red dim" href=${href}>${title}</a>
  `
}

module.exports = function main () {
  return html`
    <main class="code ma4 pa4 lh-copy bg-white mw8 ${prefix}">
      <h1 class="mt0 f1 lh-title">renee.kooi.me</h1>
      <p>
        hi. i write javascript modules. i help
        maintain ${Link('https://github.com/browserify/browserify', `${WIZARD} browserify`)}
        and ${Link('https://github.com/choojs/choo', `${TRAIN} choo`)}.
      </p>
      <p>you can check me out on the githubs: ${Link('https://github.com/goto-bus-stop', 'goto-bus-stop')}</p>
      <p>or on the stackoverflows, if that's your thing: ${Link('https://stackoverflow.com/users/591962/ren%C3%A9e', 'Rene')}</p>
      <p>if you would like to pay me to make modules for you, you can ${Link('mailto:renee@kooi.me', 'email me')}.<br />
         i have a ${Link('/cv', 'cv')} if you want to know more about me first.</p>
      <h2 class="f2 lh-title">projects</h2>
      <p>
        i've worked on a lot of things, most of which you can see on my ${Link('https://github.com/goto-bus-stop', 'github profile')}.
        these are some that i particularly like:
      </p>
      <ul>
        <li>${Link('https://wikibattle.me', 'WikiBattle')} : 1v1 races through Wikipedia pages</li>
        <li>${Link('https://wlk.yt', 'WLK')} : collaborative listening site for the WLK community</li>
        <li>${Link('https://github.com/browserify/tinyify', 'tinyify')} : all-round bundle optimization tool for browserify</li>
      </ul>
    </main>
  `
}
